.logo {
  display: flex;
  justify-content: center;

  white-space: pre;
  background: $gradient;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  @include animated-background; }

.logo--ascii {
  font-family: $monospace-font;
  line-height: 1em;
  font-weight: bold;

  font-size: 14px;
  @include breakpoint-down($breakpoint-xl) {
    font-size: 12px; }
  @include breakpoint-down($breakpoint-lg) {
    font-size: 10px; }
  @include breakpoint-down($breakpoint-md) {
    font-size: 8px; }
  @include breakpoint-down($breakpoint-sm) {
    font-size: 6px; }
  @include breakpoint-down($breakpoint-xs) {
    font-size: 4px; } }
