main {
  margin: 0 auto 80px;
  padding: 0 20px;
  max-width: $breakpoint-xl;
  @include breakpoint-down($breakpoint-xl) {
    max-width: $breakpoint-lg; }
  @include breakpoint-down($breakpoint-lg) {
    max-width: $breakpoint-md; }
  @include breakpoint-down($breakpoint-xs) {
    padding: 0 10px; } }

