@import "definitions";

@import "ext/fontawesome";

@import "blocks/colors";
@import "blocks/divider";
@import "blocks/footer_links";
@import "blocks/logo";
@import "blocks/main";


body {
  background: $background-color;
  color: $font-color;
  font-family: $body-font;
  font-size: $font-size-base;
  line-height: 1.4em; }

a {
  word-break: break-all;
  color: $color-4;
  text-decoration: none;
  &:hover, &:active {
    color: $color-3; } }

code {
  font-family: $monospace-font; }

button {
  $button-color: $color-2;
  display: inline-block;
  position: relative;
  cursor: pointer;
  border: none;
  border-radius: 25px;
  padding: 10px 50px;
  color: black;
  font-weight: bold;
  font: inherit;
  @include animated-background(14); }

h1, h2, h3, h4, h5, h6 {
  font-family: $decorative-font;
  font-weight: normal;
  line-height: 1.2em;
  margin-top: 1.8em; }

h1 {
  margin-top: 1em; }

pre {
  overflow-x: scroll;
  // hide scrollbars - different for each browser...
  &::-webkit-scrollbar {
    display: none; }
  -ms-overflow-style: none;
  scrollbar-width: none; }

@keyframes gradient-animation {
  0% {
    background-position: 0% 0%; }
  50% {
    background-position: 100% 0%; }
  100% {
    background-position: 0% 0%; } }
